export default {
	data() {
		return {
			year: ['1年', '2年', '3年', '其他'],
			info:{},
			money:'',
			number:null,
			n:0
		}
	},
	onLoad() {
		this.getData()
	},
	methods: {
		pay(){
			// 年份到时候用n判断下，然后支付成功 把vip存上 1 ，跳到my页面
			if(this.n==0||this.n==1||this.n==2){
				uni.showLoading({
				    title: '加载中',
					mask:"true"
				});
				var that = this
				uni.login({
				  provider: 'weixin',
				  success: function (loginRes) {
					that.wxcode = loginRes.code
					if(loginRes.errMsg ==="login:ok"){
						that.$api.Payway({
							user_id: uni.getStorageSync("userId"),
							pay_way:'wxpaymini',
							year: that.n+1,
							code:that.wxcode
						}).then(res1=>{
							uni.hideLoading();
							if(res1.data.code=='200'){
								uni.requestPayment({
								    provider: 'wxpay',
								    timeStamp: res1.data.data.timeStamp,
								    nonceStr: res1.data.data.nonceStr,
								    package: res1.data.data.package,
								    signType: res1.data.data.signType,
								    paySign: res1.data.data.paySign,
								    success: function (res) {
										uni.showToast({
										    title: '续费成功',
										    duration: 2000,
											icon:'none'
										});
										uni.setStorageSync("isVip",1)
										setTimeout(()=>{
											uni.switchTab({
											    url: '/pages/index/index'
											});
										},1000)
								    },
								    fail: function (err) {
								        uni.showToast({
								            title: '支付失败',
								            duration: 2000,
								        	icon:'none'
								        });
								    }
								});
							}else if(res1.data.code=='1099'){
								uni.showToast({
								    title: '注册会员需要实名认证',
								    duration: 2000,
									icon:'none'
								});
								setTimeout(function() {
									uni.reLaunch({
										url:'/pages/PersonalIdcard/PersonalIdcard'
									}); // 完善信息
								}, 1000);
							}
						})
					}
				  }
				});
			}else{
				if(this.number>3){
					uni.showLoading({
					    title: '加载中',
						mask:"true"
					});
					var that = this
					uni.login({
					  provider: 'weixin',
					  success: function (loginRes) {
						that.wxcode = loginRes.code
						if(loginRes.errMsg ==="login:ok"){
							that.$api.Payway({
								user_id: uni.getStorageSync("userId"),
								pay_way:'wxpaymini',
								year: that.number,
								code:that.wxcode
							}).then(res1=>{
								uni.hideLoading();
								if(res1.data.code=='200'){
									uni.requestPayment({
									    provider: 'wxpay',
									    timeStamp: res1.data.data.timeStamp,
									    nonceStr: res1.data.data.nonceStr,
									    package: res1.data.data.package,
									    signType: res1.data.data.signType,
									    paySign: res1.data.data.paySign,
									    success: function (res) {
											uni.showToast({
											    title: '续费成功',
											    duration: 2000,
												icon:'none'
											});
											uni.setStorageSync("isVip",1)
											setTimeout(()=>{
												uni.switchTab({
												    url: '/pages/index/index'
												});
											},1000)
									    },
									    fail: function (err) {
									        uni.showToast({
									            title: '支付失败',
									            duration: 2000,
									        	icon:'none'
									        });
									    }
									});
								}else if(res1.data.code=='1099'){
									uni.showToast({
									    title: '注册会员需要实名认证',
									    duration: 2000,
										icon:'none'
									});
									setTimeout(function() {
										uni.reLaunch({
											url:'/pages/PersonalIdcard/PersonalIdcard'
										}); // 完善信息
									}, 1000);
								}
							})
						}
					  }
					});
				}else{
					uni.showToast({
					    title: '请输入续费时长并且时间大于3年',
					    mask: "true",
						icon:'none'
					});
				}
			}
		},
		ipt(e){
			setTimeout(() => { this.number = this.number.replace(/\D/g,'') }, 0)
			//    console.log(e)
		},
		tab(index){
			this.n = index
			// this.number = index+1
		},
		getData(){
			this.$api.MyselfIndex({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId")
			}).then(res=>{
				this.info = res.data.data
				this.$api.get_money_v2({
					year: 1,
					user_id: uni.getStorageSync("userId")
				}).then(res=>{
					this.money = res.data.data.amount
				})
			})
		}
	}
}
